.contact-form {
  width: 100%;
  margin-top: 20px;
}
.contact-form ul {
  padding: 0;
  margin: 0;
}
.contact-form ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 10px;
  opacity: 0;
  overflow: hidden;
  display: block;
  clear: both;
  position: relative;
  -webkit-animation: fadeInUp 2s 2s;
          animation: fadeInUp 2s 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.contact-form ul li.half {
  width: 49%;
  margin-left: 2%;
  float: left;
  clear: none;
}
.contact-form ul li.half:first-child {
  margin-left: 0;
}
.contact-form input[type=text],
.contact-form input[type=email] {
  width: 100%;
  border: 0;
  background: #115173;
  height: 50px;
  font-size: 16px;
  color: #fff;
  padding: 0 20px;
  box-sizing: border-box;
}
.contact-form textarea {
  width: 100%;
  border: 0;
  background: #115173;
  height: 50px;
  font-size: 16px;
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  min-height: 150px;
}
.contact-form .flat-button {
  color: #ffd700;
  font-size: 11px;
  letter-spacing: 3px;
  text-decoration: none;
  padding: 8px 10px;
  border: 1px solid #ffd700;
  float: left;
  border-radius: 4px;
  background: 0 0;
  text-transform: uppercase;
  float: right;
  text-align: center;
  margin-right: 10px;
}

.map-wrap {
  background: rgba(8, 253, 216, 0.1);
  float: right;
  width: 53%;
  height: 115%;
  margin-top: -3%;
}

.leaflet-container {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-animation: backInRight 1s 1.2s;
          animation: backInRight 1s 1.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.info-map {
  position: absolute;
  background: #000;
  top: 50px;
  right: 30%;
  z-index: 999999;
  width: 267px;
  padding: 20px;
  color: #fff;
  font-family: "Helvetica";
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  -webkit-animation: fadeIn 1s 1.5s;
          animation: fadeIn 1s 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.info-map span {
  font-size: 16px;
}
.info-map span span {
  color: #ffd700;
}/*# sourceMappingURL=index.css.map */