.loader-hidden {
  display: none;
}

.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  -webkit-animation: fadeOut 1s 1s;
          animation: fadeOut 1s 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
